import axios from 'axios'
import {
  Message,
  MessageBox,
  Loading
} from 'element-ui'
import router from '../router/index'

// axios
const service = axios.create({
  // baseURL: '/', // apibase_url
  baseURL: process.env.BASE_URL_RPUSH, // 
  timeout: 30000 // 
})

let loading

// request
service.interceptors.request.use(
  config => {
    let token = localStorage.getItem('token')
    if (token) {
      config.headers.Authorization = token
      console.log('interceptors config=', config)
    }
    loading = Loading.service({
      fullscreen: true
    })
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
  }
)

// respone
service.interceptors.response.use(
  response => {
    loading.close()
    const res = response.data
    if (res.code === 401) {
      MessageBox.alert('', '', {
        confirmButtonText: '',
        callback: action => {
          router.replace({
            path: '/login'
          })
        }
      })
      return res
    } else if (res.code === 0) {
      return res
    } else {
      Message({
        message: res.msg || '',
        type: 'error'
      })
      return res
    }
  }
)

export default service



